<template>
	<view class="container">
		<!-- ‰ΩøÁî®Ëá™ÂÆö‰πâÂØºËà™Ê†èÁªÑ‰ª∂ -->
		<custom-nav-bar title="Êé¢Â∫óËØ¶ÊÉÖ" subtitle="Êü•Áúã‰ΩìÈ™å" :show-back="true"></custom-nav-bar>
		
		<!-- Ê∑ªÂä†ÊòéÊòæÁöÑËøîÂõûÊåâÈíÆ -->
		<view class="back-button" @click="goBack">
			<text class="back-arrow">‚Üê</text>
			<text class="back-text">ËøîÂõû</text>
		</view>
		
		<!-- ÊòæÁ§∫Âä†ËΩΩ‰∏≠Áä∂ÊÄÅ -->
		<view v-if="loading" class="loading-container">
			<view class="loading-spinner"></view>
			<text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
		</view>
		
		<!-- ÊòæÁ§∫Êó†Êï∞ÊçÆÁä∂ÊÄÅ -->
		<view v-else-if="!review" class="empty-container">
			<text class="empty-text">Êú™ÊâæÂà∞Êé¢Â∫óÂÜÖÂÆπ</text>
		</view>
		
		<!-- Êé¢Â∫óËØ¶ÊÉÖÂÜÖÂÆπ -->
		<view v-else class="review-detail">
			<view class="review-card">
				<view class="review-header">
					<view class="user-info">
						<image class="user-avatar" :src="review.userAvatar || 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/ht.jpg'" mode="aspectFill"></image>
						<view class="user-details">
							<text class="user-name">{{review.userName || 'ÂåøÂêçÁî®Êà∑'}}</text>
							<view class="review-rating">
								<text v-for="n in 5" :key="n" class="star" :class="{ active: n <= Math.floor(review.rating || 0) }">‚òÖ</text>
								<text class="rating-text">{{review.rating || 0}}ÂàÜ</text>
							</view>
						</view>
					</view>
					<text class="review-time">{{formatDate(review.createTime)}}</text>
				</view>
				
				<view class="review-content">
					<text class="review-title">{{review.title || 'Êé¢Â∫ó‰ΩìÈ™å'}}</text>
					<text class="review-text">{{review.content || 'ÊöÇÊó†ÂÜÖÂÆπ'}}</text>
				</view>
				
				<view v-if="review.images && review.images.length > 0" class="review-images">
					<image 
						v-for="(img, imgIndex) in review.images" 
						:key="imgIndex" 
						:src="img" 
						mode="aspectFill" 
						class="review-image-large" 
						@click="previewImage(review.images, imgIndex)"></image>
				</view>
				
				<view class="review-footer">
					<view class="action-btn" @click="likeReview()">
						<text class="action-icon" :class="{ active: review.isLiked }">üëç</text>
						<text class="action-count">{{review.likeCount || 0}}</text>
					</view>
					<view class="action-btn" @click="toggleCommentInput">
						<text class="action-icon">üí¨</text>
						<text class="action-count">{{review.commentCount || 0}}</text>
					</view>
					<view class="action-btn" @click="shareReview()">
						<text class="action-icon">üîó</text>
						<text class="action-text">ÂàÜ‰∫´</text>
					</view>
				</view>
			</view>
			
			<!-- ËØÑËÆ∫ÂàóË°® -->
			<view class="comments-section">
				<view class="section-title">
					<text class="title-text">ÂÖ®ÈÉ®ËØÑËÆ∫ ({{comments.length}})</text>
				</view>
				
				<view v-if="comments.length === 0" class="empty-comments">
					<text class="empty-text">ÊöÇÊó†ËØÑËÆ∫ÔºåÂø´Êù•ÂèëË°®Á¨¨‰∏ÄÊù°ËØÑËÆ∫Âêß</text>
				</view>
				
				<view v-else class="comments-list">
					<view class="comment-item" v-for="(comment, index) in comments" :key="index">
						<view class="comment-header">
							<image class="comment-avatar" :src="comment.userAvatar || 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/ht.jpg'" mode="aspectFill"></image>
							<view class="comment-user-info">
								<text class="comment-user-name">{{comment.userName || 'ÂåøÂêçÁî®Êà∑'}}</text>
								<text class="comment-time">{{formatDate(comment.createTime)}}</text>
							</view>
						</view>
						<view class="comment-content">
							<text class="comment-text">{{comment.content}}</text>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- ËØÑËÆ∫ËæìÂÖ•Ê°Ü -->
		<view class="comment-input-container" v-if="showCommentInput">
			<input 
				class="comment-input" 
				type="text" 
				v-model="commentText" 
				placeholder="ËØ¥ÁÇπ‰ªÄ‰πà..." 
				focus 
				@blur="onInputBlur"
				@confirm="submitComment" />
			<view class="comment-submit" @click="submitComment">ÂèëÈÄÅ</view>
		</view>
		
		<!-- AIÂä©ÊâãÁªÑ‰ª∂ -->
		<ai-assistant></ai-assistant>
	</view>
</template>

<script>
	import AiAssistant from '@/components/AiAssistant.vue';
	import CustomNavBar from '@/components/CustomNavBar.vue';
	import config from '@/config/index.js';
	
	export default {
		components: {
			AiAssistant,
			CustomNavBar
		},
		data() {
			return {
				id: null,
				loading: false,
				review: null,
				comments: [],
				showCommentInput: false,
				commentText: ''
			}
		},
		onLoad(options) {
			console.log('Êé¢Â∫óËØ¶ÊÉÖÈ°µÂèÇÊï∞:', JSON.stringify(options));
			this.id = options.id;
			
			if (!this.id) {
				console.error('Êú™Ëé∑ÂèñÂà∞Êé¢Â∫óID');
				uni.showToast({
					title: 'Êé¢Â∫óIDÊó†Êïà',
					icon: 'none'
				});
				setTimeout(() => {
					this.goBack();
				}, 1500);
				return;
			}
			
			// Â¶ÇÊûúÈúÄË¶ÅÊòæÁ§∫ËØÑËÆ∫ËæìÂÖ•Ê°Ü
			if (options.showComment === 'true') {
				this.showCommentInput = true;
			}
			
			console.log('Âä†ËΩΩÊé¢Â∫óËØ¶ÊÉÖÔºåID:', this.id);
			this.loadReviewDetail();
			this.loadComments();
		},
		methods: {
			// Âä†ËΩΩÊé¢Â∫óËØ¶ÊÉÖ
			loadReviewDetail() {
				this.loading = true;
				
				uni.request({
					url: `${config.BaseUrl}/api/shop/review/${this.id}`,
					method: 'GET',
					header: {
						'content-type': 'application/json'
					},
					success: (res) => {
						console.log('Ëé∑ÂèñÊé¢Â∫óËØ¶ÊÉÖÊàêÂäü:', JSON.stringify(res.data));
						if (res.data && res.data.code === 1) {
							const reviewData = res.data.data;
							
							// Â§ÑÁêÜÂõæÁâáÊï∞ÁªÑ
							if (typeof reviewData.images === 'string' && reviewData.images) {
								try {
									reviewData.images = JSON.parse(reviewData.images);
								} catch (e) {
									console.error('Ëß£ÊûêÂõæÁâáJSONÂ§±Ë¥•:', e);
									reviewData.images = [];
								}
							} else if (!reviewData.images) {
								reviewData.images = [];
							}
							
							// Ê∑ªÂä†ÁÇπËµûÁä∂ÊÄÅÔºàÊ®°ÊãüÔºâ
							reviewData.isLiked = false;
							
							this.review = reviewData;
						} else {
							console.error('Ëé∑ÂèñÊé¢Â∫óËØ¶ÊÉÖAPIËøîÂõûÈîôËØØ:', JSON.stringify(res.data));
							uni.showToast({
								title: 'Ëé∑ÂèñÊé¢Â∫óËØ¶ÊÉÖÂ§±Ë¥•',
								icon: 'none'
							});
						}
					},
					fail: (err) => {
						console.error('ËØ∑Ê±ÇÂ§±Ë¥•:', JSON.stringify(err));
						uni.showToast({
							title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
							icon: 'none'
						});
					},
					complete: () => {
						this.loading = false;
					}
				});
			},
			
			// Âä†ËΩΩËØÑËÆ∫
			loadComments() {
				// ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ËØÑËÆ∫APIÔºå‰ΩÜÁõÆÂâç‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ
				this.comments = [
					{
						id: 1,
						userId: 1,
						userName: 'Áî®Êà∑A',
						userAvatar: 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/ht.jpg',
						content: 'ËøôÂÆ∂Â∫óÁúüÁöÑÂæà‰∏çÈîôÔºå‰∏ãÊ¨°Ëøò‰ºöÂéªÔºÅ',
						createTime: new Date(Date.now() - 3600000).toISOString()
					},
					{
						id: 2,
						userId: 2,
						userName: 'Áî®Êà∑B',
						userAvatar: 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/ht.jpg',
						content: '‰ª∑Ê†ºÂÆûÊÉ†ÔºåÊúçÂä°ÊÄÅÂ∫¶‰πüÂæàÂ•Ω',
						createTime: new Date(Date.now() - 86400000).toISOString()
					}
				];
			},
			
			// ÁÇπËµûÊé¢Â∫ó
			likeReview() {
				if (!this.review) return;
				
				// ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÁÇπËµûAPIÔºåËøôÈáåÂè™ÂÅöÊ®°Êãü
				this.review.isLiked = !this.review.isLiked;
				
				if (this.review.isLiked) {
					this.review.likeCount = (this.review.likeCount || 0) + 1;
				} else if (this.review.likeCount > 0) {
					this.review.likeCount -= 1;
				}
				
				uni.showToast({
					title: this.review.isLiked ? 'ÁÇπËµûÊàêÂäü' : 'Â∑≤ÂèñÊ∂àÁÇπËµû',
					icon: 'none'
				});
			},
			
			// ÂàÜ‰∫´Êé¢Â∫ó
			shareReview() {
				uni.showShareMenu({
					withShareTicket: true
				});
			},
			
			// ÊòæÁ§∫ËØÑËÆ∫ËæìÂÖ•Ê°Ü
			toggleCommentInput() {
				this.showCommentInput = !this.showCommentInput;
			},
			
			// ËæìÂÖ•Ê°ÜÂ§±ÂéªÁÑ¶ÁÇπ
			onInputBlur() {
				// Áïô‰∏ÄÁÇπÂª∂ËøüÔºå‰ª•‰æøÂèØËÉΩÁöÑÁÇπÂáªÊèê‰∫§ÊåâÈíÆÁîüÊïà
				setTimeout(() => {
					if (this.commentText.trim() === '') {
						this.showCommentInput = false;
					}
				}, 200);
			},
			
			// Êèê‰∫§ËØÑËÆ∫
			submitComment() {
				if (!this.commentText.trim()) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ËØÑËÆ∫ÂÜÖÂÆπ',
						icon: 'none'
					});
					return;
				}
				
				// Ê®°ÊãüÊèê‰∫§ËØÑËÆ∫
				const newComment = {
					id: this.comments.length + 1,
					userId: 3,
					userName: 'ÂΩìÂâçÁî®Êà∑',
					userAvatar: 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/ht.jpg',
					content: this.commentText,
					createTime: new Date().toISOString()
				};
				
				this.comments.unshift(newComment);
				this.commentText = '';
				this.showCommentInput = false;
				
				// Êõ¥Êñ∞ËØÑËÆ∫Êï∞
				if (this.review) {
					this.review.commentCount = (this.review.commentCount || 0) + 1;
				}
				
				uni.showToast({
					title: 'ËØÑËÆ∫ÊàêÂäü',
					icon: 'success'
				});
			},
			
			// È¢ÑËßàÂõæÁâá
			previewImage(images, current) {
				uni.previewImage({
					urls: images,
					current: images[current]
				});
			},
			
			// Ê†ºÂºèÂåñÊó•Êúü
			formatDate(dateStr) {
				if (!dateStr) return 'Êú™Áü•Êó∂Èó¥';
				
				const date = new Date(dateStr);
				const now = new Date();
				const diff = now - date;
				
				// Â∞è‰∫é1ÂàÜÈíü
				if (diff < 60 * 1000) {
					return 'ÂàöÂàö';
				}
				
				// Â∞è‰∫é1Â∞èÊó∂
				if (diff < 60 * 60 * 1000) {
					return `${Math.floor(diff / (60 * 1000))}ÂàÜÈíüÂâç`;
				}
				
				// Â∞è‰∫é1Â§©
				if (diff < 24 * 60 * 60 * 1000) {
					return `${Math.floor(diff / (60 * 60 * 1000))}Â∞èÊó∂Ââç`;
				}
				
				// Â∞è‰∫é30Â§©
				if (diff < 30 * 24 * 60 * 60 * 1000) {
					return `${Math.floor(diff / (24 * 60 * 60 * 1000))}Â§©Ââç`;
				}
				
				// Â§ß‰∫é30Â§©ÊòæÁ§∫ÂÖ∑‰ΩìÊó•Êúü
				const year = date.getFullYear();
				const month = date.getMonth() + 1;
				const day = date.getDate();
				return `${year}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day}`;
			},
			
			goBack() {
				uni.navigateBack({
					delta: 1,
					fail: () => {
						// Â¶ÇÊûúÊ≤°Êúâ‰∏ä‰∏ÄÈ°µÔºåËøîÂõûÊé¢Â∫óÂàóË°®
						uni.switchTab({
							url: '/pages/mall/index'
						});
					}
				});
			}
		}
	}
</script>

